html {
    background-color: rgb(10,25,48);
}

.App, #app-root, .loader {
    text-align: center;
    font-family: Verdana, Geneva, sans-serif;
    /* "Lucida Sans Unicode", "Lucida Grande", sans-serif; */
    background-color: rgb(10,25,48);
    color: rgba(255,255,255,0.6);
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}


h1 {
    font-size: 2em;
    font-weight: normal;
}

h2 {
    font-size: 1em;
    font-weight: normal;
}

h3 {
    font-size: 1em;
    font-weight: normal;
}



.header {
    max-width: 70%;
    min-width: 30%;
    font-weight: normal;
    font-variant-caps: all-small-caps;
    text-align: center;
    margin: auto auto;
    padding: 5px 10px;
    border-radius: 0px;
    vertical-align: middle;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
}

.footer {
    width: 30%;
    text-align: center;
    font-size: 2em;
    margin: auto auto;
    padding: 10px;
    border-radius: 0px;
    vertical-align: middle;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
}


.thanks {
    text-align: center;
    font-size: 1em;
    margin: auto auto;
    padding: 10px;
    border-radius: 0px;
    vertical-align: middle;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

p, ol, li {
    text-align: left;
}

a {
    color: rgba(234,176,26,0.6);
    text-decoration: none;
}

a:hover, .faIcon:hover, a:active, .faIcon:active  {
    color: rgba(234,176,26,1);
    text-decoration: none;
}

.menu {
    font-size: 1.5em;
    margin: auto auto;
    padding: 0px 10px 0px;
    vertical-align: middle;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    -ms-flex-line-pack: center;
        align-content: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    min-height: 80px;
    max-width: 50%;
    min-width: 25%;
    z-index: 3;
    border: 2px solid rgba(255,255,255,0.6);
    border-radius: 24px;
    position: static;
    background-color: rgb(10,25,48);
}
.info {
    min-height: 20px;
}

.info-page {
    position: fixed;
    margin: auto;
    width: 50%;
    padding: 20px 50px;
    z-index: 4;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 2px solid rgba(255,255,255,0.6);
    border-radius: 24px;
    background-color: rgb(10,25,48);
}
.section-container {
    margin: auto auto;
    padding: 5px 10px;
    border-radius: 0px;
    vertical-align: middle;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    min-height: 400px;
    max-height: 800px;
    width: auto;
}

.horiz, .vert, .circ {
    padding: 0px;
    display: inline-block;
    position: relative;

}

.circ {
    border-radius: 50%;
}

.hide {
    display: none;
}

button {
    width: 120px;
    height: 50px;
    background-color: rgba(255,255,255,0.6);
    border: 0px;
    border-radius: 24px;
    color: rgb(10,25,48);
    text-transform: uppercase;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
}

button:hover, button:active {
    background-color:  rgba(234,176,26,1);;
}

.error {
   color: #A31621;
}
.record, .play, .pause, .clear {
    border: 2px solid rgb(10,25,48);
    padding: 10p;
}

.record {
    color: #A31621;
    border-radius: 50%;
}

.record:active, .record:hover, .recording:active, .recording:hover {
    color: #DB222A;
}

.recording {
    border: 2px solid #819E57;
    border-radius: 50%;
    color: #A31621;
}

.play:active, .play:hover {
    color: #819E57;
}


.slider {
    margin: auto;
    min-width: 50%;
    max-width: 80%;
    outline: none;
    top: 80%;
    position: fixed;
    z-index: 3;
}

.checkbox {
    height: 2.5em;
    width: 2.5em;
    padding: 0px;
    margin: 0px;
    font-size: 0.6em;
    text-align: center;
    -webkit-transition: all .2s;
    transition: all .2s;
    text-align: center;
}


input:checked ~ .checkmark {
  background-color: #86BBD8;
}


